<template>
  <!-- 商品信息 -->
  <div class="order-shopinfo">
    <div class="title">商品信息</div>

    <div class="table-content">
        <table class="table">
            <tr>
                <th style="width: 37%">名称</th>
                <th  style="width: 21%">单价</th>
                <th  style="width: 21%">数量</th>
                <th  style="width: 21%">价格</th>
            </tr>
            <tr v-for="(item, index) in list" :key="item.food_id">
                <td>{{ item.food_name }}</td>
                <td>&yen; {{ item.food_price}}</td>
                <td>{{ item.food_num }}</td>
                <td>&yen; {{ item.food_price_sum}}</td>
            </tr>
            <tr>
                <td  colspan="3">
                    商品总数
                </td>
                <td>
                    总计
                </td>
            </tr>

            <tr class="last">
                <td colspan="4" class="clearfix">
                    <div class="td-title left">备注:</div>
                    <div class="remark-content left">备注备注备注备注备注备注备注备注备dd注备注备注备注备注备注备注备注备注备dd注备注备注备注备注备注备注备注备注备dd注备注备注备注备注备注备注备注备注备dd注备注备注备注备注备注备注备注备注备dd注备注备注备注备注备注备注备注备注备dd注</div>
                </td>
            </tr>
        </table>
    </div>    
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          id: "3636",
          food_id: "21",
          food_name: "大配件",
          food_price: 12,
          food_num: 1,
          food_category: null,
          food_price_sum: 12,
          food_attach_list: null,
          food_unit: null,
          unit_num: 0
        },
        {
          id: "3643",
          food_id: "210",
          food_name: "川味小龙虾",
          food_price: 180,
          food_num: 1,
          food_category: null,
          food_price_sum: 180,
          food_attach_list: null,
          food_unit: null,
          unit_num: 0
        },
        {
          id: "3648",
          food_id: "213",
          food_name: "红烧猪蹄",
          food_price: 180,
          food_num: 5,
          food_category: null,
          food_price_sum: 900,
          food_attach_list: null,
          food_unit: null,
          unit_num: 0
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.title {
  @include fc(14px,#666);
  margin: 20px 0;
  font-weight: bold;
}
.table-content {
  //   border-top: 1px solid #d8d8d8;
  //   border-bottom
}
.table {
  @include fc(14px,#333);
  border-top: 1px solid #d8d8d8;
  border-left: 1px solid #d8d8d8;
  border-collapse: collapse;
  width: 100%;

  th {
    border-bottom: 1px solid #d8d8d8;
    font-weight: normal;
    height: 50px;
  }

  th:last-child {
    border-right: 1px solid #d8d8d8;
  }

  td {
    border-right: 1px solid #d8d8d8;
    border-bottom: 1px solid #d8d8d8;
    text-align: center;
    height: 50px;
  }
  tr.last {
    td {
      text-align: left;
      padding: 20px 40px;
      div {
        height: 100%;
        //   @include wh(100%, 100%);
      }
      div.td-title{
          padding-right: 5px;
      }
      div.remark-content{
          max-width: 90%;
      }
    }
  }
}
</style>




